import React from "react"
import { Link } from "react-router-dom"

export default function RedirectItem({ route, op }) {
    return (
        <div
            style={{
                width: "100%",
                textAlign: "center",
                marginTop: "10px",
                borderBottomWidth: "2px",
                borderBottomColor: "#eee",
                borderBottomStyle: "solid",
            }}>
            <Link
                to={route}
                style={{
                    textDecoration: "none",
                    fontSize: "16pt",
                    color: "#a3a3a3",
                }}>
                Fazer {op}
            </Link>
        </div>
    )
}
